import React, { Component } from 'react'
import $ from 'jquery'
import url from './audio/gbqq.mp3' // 引入背景音乐文件


class Main extends Component {
    state = {
        date: {},
    }
    componentDidMount() {
        this.print();
        setInterval(() => {
            this.time(2019, 12, 22) // 你们的纪念日
        }, 1000
        )
        var audio = document.getElementById("audio");
        setTimeout(() => audio.play(), 7500) // 背景音乐在页面加载后，延迟播放的时长，单位：毫秒。
    }
    print = () => {
        $.fn.autotype = function () {
            var _this = $(this);
            var str = _this.html();
            // 正则替换代码行之间添加的多个空格，不去除换行输出会有明显的停顿：实际是在输出多个空格
            str = str.replace(/(\s){2,}/g, "$1");
            var index = 0;
            $(this).html('');
            var timer = function fn() {
                var args = arguments;
                var current = str.slice(index, index + 1);
                // html标签完整输出,如：<p>
                if (current == '<') {
                    index = str.indexOf('>', index) + 1;
                }
                else {
                    index++;
                }
                //位运算符: 根据setInterval运行奇偶次来判断是否加入下划线字符“_”，使输入效果更逼真
                if (index < str.length - 1) { //打印字符倒数第2个字符开始，不加下划线字符，以防止结束符可能会多输出一下划线字符
                    _this.html(str.substring(0, index) + (index & 1 ? '_' : ''));
                } else {
                    _this.html(str.substring(0, index));
                    clearTimeout(timer);
                };
                setTimeout(fn, 200)
            };
            // 延迟1s开始
            setTimeout(timer, 1000);
        };
        $("#autotype").autotype();
    }
    time = (year, month, day) => {
        var dateNow = new Date();
        var dateJNR = new Date(year, month - 1, day);
        // var anniversary = parseInt((dateNow - dateJNR) / (365*24*3600*1000))
        var d = parseInt((dateNow - dateJNR) / (24 * 3600 * 1000));
        var hour = parseInt(((dateNow - dateJNR) / (3600 * 1000)) % 24);
        var minute = parseInt((dateNow - dateJNR) / (1000 * 60) % 60);
        var second = parseInt((dateNow - dateJNR) / 1000 % 60);
        this.setState({ date: { d: d, hour: hour, minute: minute, second: second } });
    };
    render() {
        const date = () => {
            if (this.state.date.d !== undefined) {
                const { d, hour, minute, second } = this.state.date
                return (<p>我们已经一起走过了: <span className="date-text">{d}</span> 天 <span className="date-text">{hour}</span> 小时 <span className="date-text">{minute}</span> 分 <span className="date-text">{second}</span> 秒 </p>
                )
            }
        }
        return (
            <div className="App animated bounceInLeft">
                <div className="date">{date()}</div>
                <div id="autotype">
                    <h1 style={{ fontWeight: 900 }}>你好哇，万祾小可爱！</h1>
                    <p>今天是我们一百天的纪念日^_^</p>
                    <p>
                        傻丫头，时间过的好快，不知不觉已经过了100天，但我一直觉得就像昨天刚刚认识你。这一百天来，我们有过欢乐也有过争吵，也因为一些事情让两个人互相生气，也在深夜里听过对方难过到极致的痛哭……但所幸，我们会慢慢的解决遇到的问题，而不是想着放弃；会为对方分享的事情感到快乐，而不是仅仅敷衍；会改掉自己身上让对方讨厌的小毛病，而不是得过且过。我们以后的一百年也会像过去的一百天一样。 ♪（＾∀＾●）ﾉ
                    </p>
                    <p>
                        还记得刚认识你的那个晚上，现在想想都想笑，铁骨铮铮变成了大型真香现场。还记得刚认识时你说了什么吗？真是个憨憨。╭(′▽`)╭(′▽`)╯熟悉了之后发现我们两个有那么多相似的地方，对未来的规划几乎一模一样，甚至两个人的小名都一模一样。那段时间你常说我们就像久别重逢的老友，相互之间没有陌生感。而我看到你，常常像看到大二时的我，就像这个世界上另一个我，你吸引着我，让我靠近你，靠近你…… 然后就像着了魔一样，整晚整晚失眠，白天脑海里是你，梦中是你，清晨醒来脑海里依然是你。
                    </p>
                    <p>
                        宝宝，你还记得吗？那天夜里两三点，你傻乎乎的给我发你摸肚肚的视频，看着你用肉乎乎的小手捏着胖嘟嘟的小肚子，大早上的我把寝室室友笑醒，然后一遍又一遍的看着你发的视频在那里傻笑，心想天底下怎么有这么可爱的女生。也就是从那时起萌生了一股强烈的念头，我要照顾这个女生一辈子，要和她在这个世界上走一走，要和她看看清晨的太阳和夜晚的月亮，要陪她哭陪她闹陪她一起变傻，要和她有个家。｡:.ﾟヽ(｡◕‿◕｡)ﾉﾟ.:｡+ﾟ
                    </p>
                    <p>
                        小女污，你还记得吗？第一次见面那天，我们怀着激动且怂的心情去见对方，从凌晨四点开始就一直忐忑着、憧憬着。当时从火车下车的一瞬间，我有种想要逃回火车的冲动。后来一遍遍的安慰自己“没事的，就当去看看老友，看看自己”，一遍遍从鼓起勇气到想要跑掉，然后再重新鼓起勇气……见到你的那一刻，我的心像是要从胸膛中跳出来一样，然后一整天都处于飘飘然的状态。那一天是我至今为止过得最美好的一天。 ( ˘ ³˘)♥
                    </p>
                    <p>
                        憨祾儿，我们以后很长一段时间都处于异地，我知道你的安全感差，放心吧，我有信心经营好我们这段感情，异地不会是我们感情的绊脚石，只会磨砺我们的感情，让我们的感情更加深厚，我坚信陪你熬过异地便是一生。爱你傻宝。
                    </p>
                    <p>
                        我不是一个善于表达的人，写这么多只是想表达“我爱你，想和你一直在一起，上了贼船不能逃的那种”。
                    </p>
                    <p>最后祝我家小女污一百天纪念日快乐哦＼( ^▽^ )／</p>
                    <div style={{ textAlign: 'right' }}>
                        <p>爱你的马旭小朋友</p>
                        <p>2020年03月31日</p>
                    </div>
                </div>
                <audio id="audio" loop="loop" src={url}></audio>
            </div>

        )
    }
}
export default Main