import React, { Component } from 'react';  //导入React组件
import './App.css';                        //导入CSS文件
import iconurl from './icon/爱心.png'       //导入图片
import Main from './Main'                   //导入main.js（控制主页的逻辑）
class App extends Component {
  state = {
    class: 'bounceInLeft',
    show: false,

  };

  render() {

    const main = () => {
      if (this.state.show)
        return <Main />
    }
    return (
      <div>
        <div className={"envelope animated " + this.state.class}>
          <div className="triangle-down"></div>
          <img className="heart" src={iconurl} onClick={() => {
            this.setState({ class: 'bounceOutRight', show: true })
          }} />
          <div className="triangle-up"></div>
          <div className="text">万祾小可爱，你收到一封信，点击查收！</div>
        </div>
        {main()}
      </div>
    );
  }
}

export default App;
